"use client";

import { FormEvent, useState } from "react";

export default function EmployerOnboardingPage() {
  const [companyName, setCompanyName] = useState("");
  const [contactName, setContactName] = useState("");
  const [email, setEmail] = useState("");
  const [locations, setLocations] = useState("");
  const [shiftTeamSize, setShiftTeamSize] = useState("");
  const [timeline, setTimeline] = useState("this-month");
  const [notes, setNotes] = useState("");
  const [submitted, setSubmitted] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setError(null);
    setSubmitted(false);

    if (!companyName || !contactName || !email || !locations) {
      setError("Please fill in the required fields.");
      return;
    }

    console.log("Employer onboarding", {
      companyName,
      contactName,
      email,
      locations,
      shiftTeamSize,
      timeline,
      notes,
    });

    setSubmitted(true);
  };

  return (
    <section className="families-auth">
      <div className="families-auth-inner">
        <div className="families-hero">
          <p className="eyebrow">Employers</p>
          <h1>Set up ShiftSitter for your shift teams</h1>
          <p className="lead">
            Share the basics about your workforce so we can stand up your tenant,
            verification plan, and onboarding playbook for hourly, night, and weekend
            teams.
          </p>
          <ul className="families-bullets">
            <li>Built for operations, healthcare, retail, and plant schedules.</li>
            <li>Verified access for employees plus clear guardrails for reciprocity.</li>
            <li>We coordinate matching, rollout, and communications with your HR lead.</li>
          </ul>
        </div>

        <div className="families-card">
          <div className="card-header">
            <h2>Employer onboarding intake</h2>
            <p className="muted">
              This intake sets up your ShiftSitter workspace. We will follow up with a
              short alignment call and a launch checklist.
            </p>
          </div>

          <form onSubmit={handleSubmit}>
            <div className="form-field">
              <label htmlFor="companyName">Company name</label>
              <input
                id="companyName"
                type="text"
                className="ss-input"
                placeholder="Acme Manufacturing"
                value={companyName}
                onChange={(e) => setCompanyName(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="contactName">Primary contact</label>
              <input
                id="contactName"
                type="text"
                className="ss-input"
                placeholder="Alex Rivera"
                value={contactName}
                onChange={(e) => setContactName(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="email">Work email</label>
              <input
                id="email"
                type="email"
                className="ss-input"
                placeholder="alex@acme.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="locations">Primary locations or regions</label>
              <textarea
                id="locations"
                className="ss-input"
                placeholder="Baltimore MD (warehouse + retail), York PA (plant)"
                value={locations}
                onChange={(e) => setLocations(e.target.value)}
                rows={2}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="shiftTeamSize">Approx. shift team size</label>
              <input
                id="shiftTeamSize"
                type="text"
                className="ss-input"
                placeholder="e.g., 120 hourly employees across 3 shifts"
                value={shiftTeamSize}
                onChange={(e) => setShiftTeamSize(e.target.value)}
              />
            </div>

            <div className="form-field">
              <label htmlFor="timeline">Preferred launch timeline</label>
              <select
                id="timeline"
                className="ss-input"
                value={timeline}
                onChange={(e) => setTimeline(e.target.value)}
              >
                <option value="this-month">This month</option>
                <option value="next-month">Next month</option>
                <option value="this-quarter">This quarter</option>
                <option value="exploring">Exploring options</option>
              </select>
            </div>

            <div className="form-field">
              <label htmlFor="notes">Notes or requirements (optional)</label>
              <textarea
                id="notes"
                className="ss-input"
                placeholder="Union coordination, background check requirements, data flow preferences, etc."
                value={notes}
                onChange={(e) => setNotes(e.target.value)}
                rows={3}
              />
            </div>

            {error && <p className="form-error">{error}</p>}
            {submitted && (
              <p
                style={{
                  margin: "4px 0 12px",
                  color: "#0f766e",
                  fontWeight: 700,
                }}
              >
                Thanks. Your onboarding request is in. We will reach out to align on
                rollout and next steps.
              </p>
            )}

            <div className="actions">
              <button type="submit" className="ss-btn w-100">
                Submit onboarding request
              </button>
              <p className="muted" style={{ margin: 0, fontSize: "0.95rem" }}>
                We use this information to configure access, verification, and comms.
              </p>
            </div>
          </form>
        </div>
      </div>
    </section>
  );
}
